<template>
	<div class="winningPublicityDetails">
		<div class="winningPublicityDetails_header">
			<i class="icon_arrow_left" @click="goBack"></i>
			<p class="winningPublicityDetails_title">中奖公示</p>
		</div>
		<div class="popup_main">
			<div class="popup_main_goods">
				<div class="popup_main_goods_fs">
					<img :src="item.commodityMainImg" alt="" class="img_goods">
					<div class="goods_list_box">
						<p class="text1">
							<img :src="item.logo" alt="" class="icon_logo">
							<span>{{ item.commodityName }}</span>
						</p>
						<p class="text2">
							<i class="icon_time"></i>
							<span>开奖时间：{{ item.endTime }}</span>
						</p>
						<p class="text3">
							<i class="icon_gift"></i>
							<span>共{{ item.quantity }}份奖品</span>
							<i class="icon_heat"></i>
							<span>已参与{{ item.cycleTotalInvestmentCount }}次</span>
						</p>
					</div>
				</div>
				<div class="van_swipe" v-for="(items,index) in winUserList" :key="index">
					<div class="van_swipe_text1">
						<p class="text1">中奖用户：{{ items.userName || '匿名用户' }}</p>
						<p class="text2">参与次数：{{ items.participationNum }}次</p>
					</div>
					<div class="van_swipe_text2">
						<p class="text1">中奖编号</p>
						<p class="text2">{{ items.investmentNo }}</p>
					</div>
				</div>
				<p class="goods_list_stage">第{{ item.cycle }}期</p>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		name:'winningPublicityDetails',
		data(){
			return{
				item:{},
				winUserList:{}
			}
		},
		mounted() {
			let _this = this;
			var jsonObj = localStorage.getItem("obj");
			_this.obj = JSON.parse(jsonObj);
			_this.item = _this.$route.query.item;
			_this.winningUserList(_this.item.winPrizeId);
		},
		methods:{
			goBack(){
				this.$router.go(-1);
			},
			winningUserList(winPrizeId){
				let _this = this;
				var exclusiveObj = {
					winPrizeId:winPrizeId
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/prize/winningUserList',
					data:redata,
				}).then(function(res){
					console.log('winningUserList======',res);
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						console.log('winningUserList=======',reData);
						_this.winUserList = reData;
					}
				})
			},
		}
	}
</script>

